// BCMT Practice
const BCMTPractice = () => {
  const [step, setStep] = useState(0);
  const [stimIndex, setStimIndex] = useState(0);
  const [showButtons, setShowButtons] = useState(false);
  const [responses, setResponses] = useState([]);
  const [startTime, setStartTime] = useState(null);
  const [bcmtBtnState, setBcmtBtnState] = useState('idle');
  const navigate = useNavigate();
  const timeoutRef = useRef(null);
  const isLandscape = useOrientation();
  const bcmtStimuli = [
  "", "", " ", "teacher", "singer", "", "mountain", "", "", ""];
  const correctAnswers = [null, 1, 0, 1, 0, 1, 1, 0, 1, 0]


  useEffect(() => {
    setShowButtons(false);

    if (stimIndex < bcmtStimuli.length) {
      if (step % 2 === 0) {
        timeoutRef.current = setTimeout(() => setStep(step + 1), 500);
      } else {
        setShowButtons(true);
        setStartTime(Date.now());
        timeoutRef.current = setTimeout(() => {
          setShowButtons(false);
          setResponses(prev => [...prev, { correct: false, rt: null, userAnswer: null }]);
          setStep(step + 1);
          setStimIndex(stimIndex + 1);
        }, 1000);
      }
    }
    return () => clearTimeout(timeoutRef.current);
  }, [step, stimIndex]);

  const handleResponse = (btnIdx) => {
    if (!showButtons) return;
    clearTimeout(timeoutRef.current);
    const rt = Date.now() - startTime;
    const correct = btnIdx === correctAnswers[stimIndex];
    setResponses(prev => [...prev, { correct, rt, userAnswer: btnIdx }]);
    setShowButtons(false);
    setStep(step + 1);
    setStimIndex(stimIndex + 1);
  };

  if (stimIndex >= bcmtStimuli.length) {
    const rightNum = responses.filter(r => r.correct).length;
    const totalResp = responses.length;
    const accuracy = totalResp
      ? (((rightNum+1) / totalResp) * 100).toFixed(2)
      : "0.00";
    const meanRT = responses.filter(r => r.rt !== null).length
      ? (responses.filter(r => r.rt !== null).reduce((sum, r) => sum + r.rt, 0) / responses.filter(r => r.rt !== null).length).toFixed(3)
      : "0.000";

    return (
      <div style={{
        textAlign: "center",
        color: "white",
        background: "black",
        fontSize: 40,
        alignItems: "center",
        justifyContent: "center",
        height: "100vh",
        display: "flex",
        flexDirection: "column"
      }}>
        <h2 style={{ margin: 0 }}>Complete!</h2>
        <div style={{ marginTop: 20, display: "flex", flexDirection: "column", fontSize: 20 }}>
          <div>Accuracy: <b>{accuracy}%</b></div>
          <div>Mean Response Time: <b>{meanRT} ms</b></div>
        </div>
        <div style={{display: "flex", justifyContent: "center", gap: 80 , marginTop: 50}}>
          <ReactiveButton
            buttonState={bcmtBtnState}
            idleText={<span><SlActionUndo /> Back</span>}
            color="blue"
            rounded
            size="middle"
            display = "flex"
            style={{ minWidth: 40 }}
            onClick={() => navigate('/')}
          />
          <ReactiveButton
            buttonState={bcmtBtnState}
            idleText={<span><FiRefreshCw /> Retry</span>}
            color="blue"
            rounded
            size="middle"
            display = "flex"
            style={{ minWidth: 40}}
            onClick={() => navigate('/BCMT')}
          />
        </div>
      </div>
    );
  }

  return (
    <div style={{
      background: "black",
      height: "100vh",
      width: "100vw",
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      justifyContent: "center"
    }}>
      <div style={{ flex: 0, display: "flex", alignItems: "center", justifyContent: "center", width: "100%", marginTop: "150px", marginBottom: showButtons ? 0: 0}}>
        {step % 2 === 1 && (
          <span style={{ color: "white", fontSize: isLandscape? "min(20vh, 60px)" : "min(20vw, 100px)", fontWeight: "bold", textAlign: "center", width: "100%"}}>
            {bcmtStimuli[stimIndex]}
          </span>
        )}
      </div>
      {showButtons && (
        <div style={{ marginBottom: 80, display: "flex", gap: 100 }}>
          <img
            src={button1}
            alt="button1"
            className="button-group"
          //   style={{
          //   width: "30vw", maxWidth: "200px", minWidth: "80px",
          //   height: "auto", display: "block", cursor: "pointer"
          // }}
            onClick={() => handleResponse(0)}
          />
          <img
            src={button2}
            alt="button2"
            className="button-group"
          //   style={{
          //   width: "30vw", maxWidth: "200px", minWidth: "80px",
          //   height: "auto", display: "block", cursor: "pointer"
          // }}
            onClick={() => handleResponse(1)}
          />
        </div>
      )}
    </div>
  );
};


